"use client";

import { LazorkitProvider } from "@lazorkit/wallet";

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en">
      <body>
        <LazorkitProvider
          rpcUrl="https://api.devnet.solana.com"
          paymasterUrl="https://demo-paymaster-url" //  for gasless tx
        >
          {children}
        </LazorkitProvider>
      </body>
    </html>
  );
}
